.active{
    display: flex;
    flex-direction: column;
    border: 1px solid lightblue;
    width: 50%;
    height: 40vh;
    background-color: rgb(225,230,254);
    border-radius: 3px;
}
.top{
    color: white;
    background-color: rgb(48, 116, 71);
    height: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top-left-radius: 3px;       
    border-top-right-radius: 3px;       
}
.bottom{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px;
}
.paypal{
    display: flex;
}
.paypal img{                
    height: 25px;
    width: 50px;
    cursor: pointer;
    border-radius: 5px;                  
}
.orders{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.orders button{
    padding: 0px 0px 0px;
}
.info{
    color: blue;  
